"use client";
import Link from "next/link";
import React, { useCallback } from "react";
import { useRouter } from "next/navigation";
import { useLenis } from "@studio-freight/react-lenis";

// Utility for smooth sleep
function sleep(ms) {
  return new Promise((resolve) => setTimeout(resolve, ms));
}

// Add a custom overlay with a white background and a centered image (page transition symbol)
function addSlideUpOverlay() {
  const old = document.getElementById("page-transition-slide-up");
  if (old) old.remove();

  const overlay = document.createElement("div");
  overlay.id = "page-transition-slide-up";
  overlay.className = "page-transition-slide-up";
  overlay.style.background = "#fff"; // White background
  overlay.style.animationDuration = "0.7s";
  overlay.style.animationTimingFunction = "cubic-bezier(0.77, 0, 0.175, 1)";
  overlay.style.display = "flex";
  overlay.style.alignItems = "center";
  overlay.style.justifyContent = "center";

  // Add the image in the exact center
  const img = document.createElement("img");
  img.src = "/svg/AVASA.svg"; // Use your page transition symbol here
  img.alt = "Page Transition Symbol";
  img.style.width = "80px";
  img.style.height = "80px";
  img.style.objectFit = "contain";
  img.style.display = "block";
  img.style.zIndex = "10000";
  overlay.appendChild(img);

  document.body.appendChild(overlay);
}

function removeSlideUpOverlay() {
  const overlay = document.getElementById("page-transition-slide-up");
  if (overlay) overlay.remove();
}

// Inject the slide-up animation CSS if not already present
if (typeof window !== "undefined" && typeof document !== "undefined") {
  if (!document.getElementById("page-transition-slide-up-style")) {
    const style = document.createElement("style");
    style.id = "page-transition-slide-up-style";
    style.innerHTML = `
      .page-transition-slide-up {
        pointer-events: none;
        position: fixed;
        inset: 0;
        z-index: 9999;
        background: #fff; /* White */
        display: flex;
        align-items: center;
        justify-content: center;
        animation: slideUpIn 0.7s cubic-bezier(0.77, 0, 0.175, 1) forwards;
      }
      .page-transition-slide-up img {
        width: 80px;
        height: 80px;
        object-fit: contain;
        display: block;
        z-index: 10000;
      }
      @keyframes slideUpIn {
        0% {
          transform: translateY(100%);
          opacity: 1;
        }
        100% {
          transform: translateY(0%);
          opacity: 1;
        }
      }
    `;
    document.head.appendChild(style);
  }
}

export const TransitionLink = ({ children, href, ...props }) => {
  const router = useRouter();
  const lenis = useLenis();

  const handleTransition = useCallback(
    async (e) => {
      // Only handle left click, no modifier keys, and internal links
      if (
        e.defaultPrevented ||
        e.button !== 0 ||
        e.metaKey ||
        e.altKey ||
        e.ctrlKey ||
        e.shiftKey ||
        (typeof href === "string" &&
          (href.startsWith("http") || href.startsWith("mailto:")))
      ) {
        return;
      }

      e.preventDefault();

      // Smoothly scroll to top before transition (if Lenis is available)
      if (lenis) {
        await lenis.scrollTo(0, {
          duration: 0.5,
          easing: (t) => 1 - Math.pow(1 - t, 3),
        });
        await sleep(20);
      }

      // Add the slide-up overlay
      addSlideUpOverlay();

      // Wait for the animation to reach the top (as soon as overlay covers the screen)
      await sleep(650);

      // Instantly navigate to the new page as soon as overlay reaches the top
      router.push(href);

      // Remove overlay after a short delay (in case of fast navigation)
      setTimeout(removeSlideUpOverlay, 500);
    },
    [router, href, lenis]
  );

  return (
    <Link {...props} href={href} onClick={handleTransition}>
      {children}
    </Link>
  );
};
